<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">作业详情</p>
            <p class="navBtn f24 cfff pl-30" @click="tuenTOedit">编辑</p>
        </div>
        <div class="setBox">
            <div class="title-detail pl-30 f28 c333 pt-20 pb-30">{{homeworkDetail.title}}</div>
            <p class="f18 c999 pl-30">{{ homeworkDetail.createTime | fileTime(homeworkDetail.createTime) }}</p>
            <div class="borderBo c666 f26 pl-30 pt-40 pr-30 pb-40">{{homeworkDetail.workDesc}}</div>
            <div class="upload">
                <p class="pl-30 f28 c333 pt-20 pb-30">图片</p>
                <div class="iploadBox">
                    <div class="imgShowBox" v-for="(item,index) in homeworkDetail.images" :key="index" @click="show(homeworkDetail.images)">
                        <div class="imgShow">
                            <img :src="$api.fileUrl + item" alt="">
                        </div>
                    </div>
                </div>
            </div>
            <div class="upload mt-30">
                <p class="pl-30 f28 c333 pt-20 pb-30">视频</p>
                <div class="iploadBox">
                    <div class="imgShowBox" v-for="(item,index) in homeworkDetail.videos" :key="index">
                        <div class="imgShow">
                            <img :src="$api.fileUrl + item" alt="">
                            <img class="play" src="../../../assets/index/play.png" alt="">
                        </div>
                    </div>
                </div>
            </div>
           
        </div>
       
    </div>
</template>

<script>

import {publisFn} from '../../../lib/util'
import { ImagePreview } from 'vant';
export default {
  name: 'HelloWorld',
    data () {
        return {
            chooseClass:'选择班级',
            chooseSubject:'选择科目',
            modelShow:false,
            modelShow1:false,
            columns: ['全部科目', '化学', '英语', '语文', '数学', '美术'],
            columnClass: ['选择科目', '2222', '英语', '语文', '数学', '美术'],
            homeworkDetail:{}
        }
    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num);
        }
    },
    methods : {
       returns(){
            this.$router.push({path:'/homework'});//返回上一层
        },
        deleteThis(){
            alert('删除')
        },
        onConfirm(value, index) {
            this.$toast(`当前值：${value}, 当前索引：${index}`);
            this.modelShow = !this.modelShow;
            this.chooseClass = value;
        },
        onCancel(picker, value, index) {    
            this.modelShow = !this.modelShow;
        },
        onConfirm1(value, index) {
            this.$toast(`当前值：${value}, 当前索引：${index}`);
            this.modelShow1 = !this.modelShow1;
            this.chooseSubject = value;
        },
        onCancel1(picker, value, index) {    
            this.modelShow1 = !this.modelShow1;
        },
        tuenTOedit(){//跳转到编辑页面
            this.$router.push({path:'/sethomework',query:{id:this.$route.query.id}})
        },
        getHomeworkDetail(){//获取作业详情
            this.$toast({
                type:'loading',
                duration:0
            })
            this.$http({url:this.$api.homework.getHomeworkDetail},{homeworkId:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.homeworkDetail = data.data;
                }else{
                    this.$toast(data.msg);
                }
                this.$toast.clear();
            })
        },
        show(url){
            var arr = []
            for(var i in url){
                arr[i] = this.$api.fileUrl + url[i]
            }
            ImagePreview([
                arr
            ]);
        }
    },
    created() {
        this.getHomeworkDetail();//获取作业详情
        console.log(publisFn)
    },
}
</script>

<style scoped>
.setBox{position: absolute;top: 0.84rem;left: 0;right: 0;bottom: 0;overflow-y: auto;}
.setTitle{height: 1.11rem;border-bottom: 1px solid #f2f2f2;line-height: 1.11rem;overflow: hidden;}
.borderBo{border-bottom: 1px solid #f2f2f2;}
.oinp{float: right;border: 0;outline: 0;width: 80%;text-align: right;padding-right: 0.2rem}
.home-comntent{height: 3.74rem;border-bottom: 1px solid #f2f2f2;}
.textarea{width: 100%;height: 1.1rem;outline: 0;border: 0;box-sizing: border-box;resize:none;}
.iploadBox{margin-left: 0.3rem;overflow: hidden;}
.imgShowBox{width: 20%;float: left;position: relative;}
.imgShow{width: 1.2rem;height: 1.2rem;border-radius: 5px;overflow: hidden;float: left;position: relative;}
.imgShow img{width: 100%;height: 100%;display: block;}
.closeX{position: absolute;top: 0;right: 0;padding: 0.05rem 0.1rem;}
.borderTop{border-top: 1px solid #f2f2f2;}
.play{position: absolute;width: 0.78rem !important;height: 0.78rem !important;left: 50%;top: 50%;margin: -0.39rem 0 0 -0.39rem}
.msgTo{width: 0.3rem;height: 0.3rem;float: right;margin: 0.35rem 0.3rem 0 0;}
.centerBtn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;position: fixed;bottom: 0.6rem;left: 50%;margin-left: -3.45rem; }
</style>





